.navbarMobile {
    display: none;
}



@media (max-width: 1432px) {

    .navbarMobile {
        display: flex;
        position: relative;
        justify-content: space-between;
        margin: 0;
    }

    .menu.open {
        top: 0 !important;
    }

    .logo {
        background-image: url('../../../img/white_logo.svg');

        width: 50px;
        height: 62px;
        z-index: 15;
        margin-top: 25px;
        margin-left: 25px;
      }

    .navAdmin .menu_btn {
        background-image: url('../../../img/burger-menu_black.svg') ; 
        color: black;
        width: 50px;
        height: 50px;
        margin-top: 25px;
        margin-right: 25px;
        background-repeat: no-repeat;
        background-position: center;
        z-index: 1000;
    }

    @media screen and (max-width: 1432px) {
        .menu.open {
            top: -50px !important;
        }
    }

