    .menu h1 {
        font-size: 44px;
    }

    .menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #1A2E47;
        position: absolute;
        margin: 0;
        width: 100%;
        top: -1500px;
        left: 0;
        height: 860px;
        padding: 0;
        z-index: 10;
        justify-content: center;
        color: #798A9E;
        transition: top 0.3s ease-in-out;
    }

    .menu ul {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .menu ul li {
        list-style: none;
        text-align: center;
    }

    .menu li a {
        text-decoration: none;
        color: #798A9E;
        font-size: 36px;
    }

    .number {
        color: #798A9E !important;
        font-size: 32px; 
        position: absolute;
        bottom: 35px;
        font-weight: 400;
        letter-spacing: 1px;
    }
}